.contactForm {
    display: flex;
    justify-content: center;
    height: 65vh;
 
  }
  .contactContainer {
    max-width: 400px;
  }
  .contactHeader {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }
  .contactHeader .btn {
    height: 40px;
    border-radius: 15px;
  }
  #contact input[type="text"],
  #contact input[type="email"],
  #contact input[type="tel"],
  #contact input[type="url"],
  #contact textarea,
  #contact button[type="submit"] {
    font: 400 12px/16px "Roboto", Helvetica, Arial, sans-serif;
  }
 
  #contact {
    border-radius: 25px;
    background: #f9f9f9;
    padding: 25px;
    position: relative;
    top: 105px;
  }
 
  #contact h3 {
    display: block;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 10px;
  }
 
  #contact h4 {
    margin: 5px 0 15px;
    display: block;
    font-size: 13px;
    font-weight: 400;
  }
 
  fieldset {
    border: medium none !important;
    margin: 0 0 10px;
    min-width: 100%;
    padding: 0;
    width: 100%;
  }
 
  #contact input[type="text"],
  #contact input[type="email"],
  #contact input[type="tel"],
  #contact input[type="url"],
  #contact textarea {
    width: 100%;
    border: 1px solid #ccc;
    background: #fff;
    margin: 10px auto;
    padding: 10px;
  }
 
  #contact textarea {
    height: 100px;
    max-width: 100%;
    resize: none;
  }
 
  #contact button[type="submit"] {
    cursor: pointer;
    width: 100%;
    border: none;
    background: #4caf50;
    color: #fff;
    margin: 0 0 5px;
    padding: 10px;
    font-size: 15px;
  }
 
  #contact button[type="submit"]:hover {
    background: #43a047;
    -webkit-transition: background 0.3s ease-in-out;
    -moz-transition: background 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out;
  }
 
  #contact button[type="submit"]:active {
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5);
  }
 
  .copyright {
    position: relative;
    top: 10px;
    text-align: center;
  }
 
  #contact input:focus,
  #contact textarea:focus {
    outline: 0;
    border: 1px solid #aaa;
  }
 
  ::-webkit-input-placeholder {
    color: #888;
  }
 
  :-moz-placeholder {
    color: #888;
  }
 
  ::-moz-placeholder {
    color: #888;
  }
 
  :-ms-input-placeholder {
    color: #888;
  }
 
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
 
    to {
      opacity: 1;
    }
  }